// ** Reactstrap Imports
import { Row, Col, Form, Button, Modal, Input, Label, ModalBody, ModalHeader, Spinner, Badge } from 'reactstrap'
import { useForm, Controller } from 'react-hook-form'
import { selectThemeColors } from '@utils'
import { useState, useEffect } from 'react'
import {  useHistory } from 'react-router-dom'
import {useDispatch, useSelector } from 'react-redux'
import Select from 'react-select'
import { toast, Slide } from 'react-toastify'
import { addProduct } from './store/productSlice'
import { getCategory } from '../category/store/categorySlice'

const ToastContent = ({ name}) => (
  <>
    <div className='toastify-header'>
      <div className='title-wrapper'>
        <h6 className='toast-title fw-bold'>You have successfully Added Product {name.toUpperCase()}</h6>
      </div>
    </div>
  </>
)
const ToastError = ({ name}) => (
  <>
    <div className='toastify-header'>
      <div className='title-wrapper'>
        <h6 className='toast-title fw-bold'>Error Added Product {name.toUpperCase()}</h6>
      </div>
    </div>
  </>
)

const Add = () => {
    // ** State
    const dispatch = useDispatch()
    const category = useSelector(state => state.category)
    const [selectedCategory, setSelectedCategory] = useState({ value: '', label: 'select category' })
    const [selectedFiles, setSelectedFiles] = useState([require('@src/assets/images/avatars/avatar-blank.png').default])
    const [categoryOptions, setCategoryOptions] = useState([])
    const [show, setShow] = useState(false)
    const [formFields, setFormFields] = useState({
      columnName: "",
      columnValue: ""
    })
    const [attributes, setAttributes] = useState([])
    const { columnName, columnValue} = formFields
    const history = useHistory()
      // ** Hook
    const {
    reset,
    control,
    setError,
    handleSubmit,
    formState: { errors }
    } = useForm({
    defaultValues: {
      name: '',
      color: '',
      description: '',
      price : '',
      quantity : ''
  }
    })

    const handleImgReset = () => {
      setSelectedFiles([require('@src/assets/images/avatars/avatar-blank.png').default])
    }  
      // handle the cloud upload
    const imageUpload = async (image) => {
    const formData = new FormData()
    formData.append("file", image)
    formData.append("upload_preset", "vgvxg0kj")
    const res = await fetch(
      "https://api.cloudinary.com/v1_1/djo5zsnlq/image/upload",
      {
        method: "post",
        body: formData
      }
    )
    const myImage = await res.json()
    return myImage.url
  }

    const handlePreview = async (e) => {
      setSelectedFiles([])
      const files = e.target.files
      const filePreviews = []
      const dummyPreview = []
      const iterator = files.length > 5 ? 5 : files.length
      for (let i = 0; i < iterator; i++) {
        dummyPreview.push('')
        if (dummyPreview.length === iterator) {
          setSelectedFiles(dummyPreview)
        }
      }
      for (let i = 0; i < iterator; i++) {
        const img = await imageUpload(files[i])
        // filePreviews.push(img)
        filePreviews.splice(i, 1, img)
        if (filePreviews.length === iterator) {
          setSelectedFiles(filePreviews)
        }
      }
      console.log(filePreviews)
    }

    const handleChange = (e) => {
      setFormFields((prevValue) => ({
        ...prevValue,
        [e.target.name]: e.target.value
      }))
    }
    const handleAttribute = async () => {
      const attribute = {
        [columnName]: columnValue
      }
      await setAttributes([...attributes, attribute])
      console.log(attributes)

    }
    const onSubmit = data => {
      const image = selectedFiles.join(", ")
      let attributeObj = []
      // attributeObj create subdocument of attributes
      if (attributes.length > 0) {
        attributeObj = attributes.map(attribute => {
          const obj = Object.entries(attribute).map(([key, value]) => {
            return {
              columnName: key,
              columnValue: value
            }
          })
          return obj.pop()
        })
      }
    
      if (Object.values(data).every(field => field.length > 0)) {
        const color = data.color.split(",").map(c => c.trim())
        dispatch(addProduct({
          ...data,
          color,
          image,
          category : selectedCategory.value,
          attribute : attributeObj
        })).then(res => {
          console.log(res.payload)
          if (res.payload.error) {
            toast.error(
              <ToastError name={res.payload.error || res.payload.username || 'John Doe'} />,
              { icon: false, transition: Slide, hideProgressBar: true, autoClose: 2000 }
            )
          } else {
            history.push("/product")
            toast.success(
            <ToastContent name={res.payload.name || res.payload.username || 'John Doe'} />,
            { icon: false, transition: Slide, hideProgressBar: true, autoClose: 2000 }
          )
          }
        })
      } else {
        for (const key in data) {
          if (data[key].length === 0) {
            setError(key, {
              type: 'manual'
            })
          }
        }
      }
    }

    const handleReset = () => {
        reset({
            name: '',
            color: '',
            description: ''
        })
      }

      useEffect(() => {
        dispatch(getCategory())

      }, [dispatch, categoryOptions])
      
      useEffect(() => {
        if (category.categories.length > 0) {
          const newCategory = []
          category.categories.forEach((cat, ind) => {
            if (cat.status === true) {
              newCategory.push({
                value: `${cat._id}`, label: `${cat.category}`
              })
              if ((category.categories.length - 1) === ind) {
                setCategoryOptions(newCategory)
              }
            }
          })
        }
      }, [category.categories.length])
      
      const addAttributesToField = () => {
        if (attributes.length > 0) {
          const badgeElements = attributes.map((attribute, index) => (
            <div key={index}>
              {Object.entries(attribute).map(([key, value]) => (
                <Badge key={key} className={`text-capitalize bg-light-secondary me-1 pill`}>
                  {`${key}: ${value}`}
                </Badge>
              ))}
            </div>
          ))

          return (
            <div className='d-flex'>
              {badgeElements}
            </div>
          )
        }
      }

    return ( 
        <>
          <div className='text-center mb-2'>
            <h1 className='mb-1'>Add Product Information</h1>
          </div>
          <div className='d-flex flex-wrap'>
                {selectedFiles.map((file, index) => (
                  <div className='d-flex me-25' key={index}>
                    {file === "" ? <div className="d-flex justify-content-center align-items-center" style = {{width : '100px', height :'100px'}}> <Spinner className='me-50'/> </div> : <img className='rounded me-50' src={file === undefined ? "" : file} alt='Generic placeholder image' height='100' width='100' />}
                  </div>
                ))}
            
            <div className='d-flex align-items-end mt-75 ms-1' style={{width:"100%"}}>
              <div>
                <Button tag={Label} className='mb-75 me-75' size='sm' color='primary'>
                  Upload
                  <Input type='file' onChange={handlePreview} hidden accept='image/*' multiple/>
                </Button>
                <Button className='mb-75' color='secondary' size='sm' outline onClick={handleImgReset}>
                  Reset
                </Button>
                <p className='mb-0'>Allowed JPG, GIF or PNG. Max size of 800kB</p>
              </div>
            </div>
          </div>
          <Form onSubmit={handleSubmit(onSubmit)}>
            <Row className='gy-1 pt-75'>
              <Col md={6} xs={12}>
                <Label className='form-label' for='name'>
                  Name
                </Label>
                <Controller
                  control={control}
                  type='name'
                  id='name'
                  name='name'
                  render={({ field }) => (
                    <Input {...field} id='name' placeholder='Doe' invalid={errors.name && true} />
                  )}
                />
              </Col>
              <Col md={6} xs={12}>
                <Label className='form-label' for='price'>
                    Price
                </Label>
                <Controller
                  control={control}
                  type='price'
                  id='price'
                  name='price'
                  render={({ field }) => (
                    <Input {...field} id='price' placeholder='1' invalid={errors.price && true}/>
                  )}
                />
              </Col>
              <Col xs={6}>
                <Label className='form-label' for='color'>
                  Color
                </Label>
                <Controller
                  control={control}
                  id='color'
                  name='color'
                  render={({ field }) => (
                    <Input {...field} id='color' placeholder='black, blue' invalid={errors.color && true}/>
                  )}
                />
              </Col>
              <Col xs={6}>
                <Label className='form-label' for='quantity'>
                  Quantity
                </Label>
                <Controller
                  control={control}
                  id='quantity'
                  name='quantity'
                  render={({ field }) => (
                    <Input {...field} id='quantity' placeholder='1' invalid={errors.quantity && true}/>
                  )}
                />
              </Col>
              <Col className='mb-1' md='12' sm='12'>
                <Label className='form-label' for='category'>
                  Category
                </Label>
                <Select
                  id='category'
                  isClearable={false}
                  className='react-select'
                  classNamePrefix='select'
                  options={categoryOptions}
                  theme={selectThemeColors}
                  defaultValue={selectedCategory}
                  onChange={option => setSelectedCategory(option)}
                />
              </Col>
              <Col md='12' sm='12' xs='12'>
                <Label className='form-label' for='description'>
                  Description
                </Label>
                <Controller
                  control={control}
                  id='description'
                  name='description'
                  render={({ field }) => (
                    <Input {...field} type='textarea' id='description' placeholder='' invalid={errors.description && true}/>
                  )}
                />
              </Col>
              <Col className='mb-1' md='12' sm='12'>
                <Label className='form-label' for='size'>
                  Attributes:
                </Label>
                {attributes.length > 0 ? (addAttributesToField()) : null}
              </Col>
              <Col xs={12} className='text-center mt-2 pt-50'>
                <Button type='submit' className='me-1' color='primary'>
                  Submit
                </Button>
                <Button
                  type='reset'
                  color='secondary'
                  outline
                  onClick={() => {
                    handleReset()
                    history.push('/product')
                  }}
                >
                  Discard
                </Button>
                <Button type='button' className='ms-1' color='primary' 
                 onClick={() => {
                  setShow(true)
                }}
                >
                  Add Attributes
                </Button>
              </Col>
            </Row>
          </Form>

          <Modal isOpen={show} toggle={() => setShow(!show)} className='modal-dialog-centered modal-lg'>
            <ModalHeader className='bg-transparent' toggle={() => setShow(!show)}></ModalHeader>
            <ModalBody className='px-sm-5 pt-50 pb-5'>
              <div className='text-center mb-2'>
                <h1 className='mb-1'>Add Attributes</h1>
              </div>
              <Form>
                <Row className='gy-1 pt-75'>
                  <Col md={6} xs={12}>
                    <Label className='form-label' for='columnName'>
                      Column Name
                    </Label>
                    <Input id='columnName'  name='columnName' placeholder='size' onChange={handleChange} value={columnName}/>
                  </Col>
                  <Col md={6} xs={12}>
                    <Label className='form-label' for='columnValue'>
                      Column Value
                    </Label>
                    <Input id='columnValue'  name='columnValue' placeholder='sm' onChange={handleChange} value={columnValue}/>
                  </Col>
                  
                  <Col xs={12} className='text-center mt-2 pt-50'>
                    <Button type='button' className='me-1' color='primary'
                    onClick={() => {
                      handleAttribute()
                      setFormFields({
                        columnName: "",
                        columnValue: ""
                      })
                      setShow(false)
                    }}
                    >
                      Submit
                    </Button>
                  </Col>
                </Row>
              </Form>
            </ModalBody>
          </Modal>
        </>
     )
}
 
export default Add